import Slider from "react-slick";
import CarouselItem from '../../components/CaruselItem'
import CarouselArr from './CarouselItemArr/CarouselArr'
import './Carousel.scss'

const Carousel = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 9,
        slidesToScroll: 4,
    };
    return (
        <Slider {...settings}>
            {
                CarouselArr.map((item) => (
                    <CarouselItem
                        imgLink={item.image}
                        title={item.title}
                    />
                ))
            }
        </Slider>
    )
}

export default Carousel