import Post from '../../components/Post'
import { Helmet } from 'react-helmet'
import './HomePage.scss'
import Carousel from '../../containers/Carousel'
import CarouselArr from '../../containers/Carousel/CarouselItemArr/CarouselArr'
import CarouselItem from '../../components/CaruselItem'
import { useState } from 'react';
import NewPost from '../../containers/NewPost'
import BestPost from '../../containers/BestPost'
import SecondCarousel from '../../containers/SecondCarousel'
import Footer from '../../containers/Footer1'
import WathchedArr from './WatchedArr/WatchedArr'
import ExtraFooter from '../../containers/Footer2'

const HomePage = (props) => {
    const { token } = props
    const [status, setStatus] = useState(true)
    const [secondStatus, SetSecondStatus] = useState('watched')

    return (
        <>
            <Helmet>
                <title>Qiwi | Home</title>
            </Helmet>
            <section className={`page-wrapper ${token ? 'active' : ''}`} >
                <div className="container">
                    <Carousel>
                        {
                            CarouselArr.map((item) => (
                                <CarouselItem
                                    imgLink={item.image}
                                    title={item.title}
                                />
                            ))
                        }
                    </Carousel>
                    <div className="post-wrapper">
                        <div className="post-type">
                            <div className="top">
                                <button onClick={() => setStatus(true)} className={`post-status ${status === true ? 'active' : ' '}`}>Новые объявления</button>
                                <button onClick={() => setStatus(false)} className={`post-status ${status === false ? 'active' : ' '}`}>Лучщие предложение</button>
                            </div>
                            <div className="bottom">
                                <div className={`active-new ${status === true ? 'active' : ' '}`}></div>
                                <div className={`active-best ${status === false ? 'active' : ' '}`}></div>
                                <div className="line"></div>
                            </div>
                        </div>
                        <div className="article">
                            {
                                status === true ? (
                                    <NewPost />
                                ) : (
                                    <BestPost />
                                )
                            }
                        </div>
                        <div className="post-wrapper-bottom">
                            <div className="line"></div>
                            <a href="#">Покозать еще</a>
                        </div>
                    </div>
                </div>
                <div className="second-carousel-wrapper">
                    <div className="container">
                        <SecondCarousel />
                    </div>
                </div>
                <div className="bottom-home">
                    <div className="container">
                        <div className="post-type">
                            <div className="top">
                                <button onClick={() => SetSecondStatus('watched')} className={`post-status ${secondStatus === 'watched' ? 'active' : ' '}`}>Просмотренные</button>
                                <button onClick={() => SetSecondStatus('selected')} className={`post-status ${secondStatus === 'selected' ? 'active' : ' '}`}>Выбор редакции</button>
                                <button onClick={() => SetSecondStatus('price')} className={`post-status ${secondStatus === 'price' ? 'active' : ' '}`}>Сниженные цены</button>
                            </div>
                            <div className="bottom">
                                <div className={`active-new ${secondStatus === 'watched' ? 'active' : ' '}`}></div>
                                <div className={`active-best ${secondStatus === 'selected' ? 'active' : ' '}`}></div>
                                <div className={`active-third ${secondStatus === 'price' ? 'active' : ' '}`}></div>
                                <div className="line"></div>
                            </div>
                        </div>
                        <div className="article">
                            {
                                secondStatus === 'watched' ? (
                                    <>
                                        <div className='subtitle'>
                                            <h1>Просмотренные</h1>
                                        </div>
                                        {
                                            WathchedArr.map(item => (
                                                <Post
                                                    img={item.image}
                                                    name={item.name}
                                                    time={item.date}
                                                    price={item.price}
                                                />
                                            ))
                                        }
                                    </>
                                ) : (<></>)
                            }
                            {
                                secondStatus === 'selected' ? (
                                    <>
                                        <div className='subtitle'> 
                                            <h1>Выбор редакции</h1>
                                        </div>
                                        {
                                            WathchedArr.map(item => (
                                                <Post
                                                    img={item.image}
                                                    name={item.name}
                                                    time={item.date}
                                                    price={item.price}
                                                />
                                            ))
                                        }
                                    </>
                                ) : (<></>)
                            }
                            {
                                secondStatus === 'price' ? (
                                    <>
                                        <div className='subtitle'>
                                            <h1>Сниженные цены</h1>
                                        </div>
                                        {
                                            WathchedArr.map(item => (
                                                <Post
                                                    img={item.image}
                                                    name={item.name}
                                                    time={item.date}
                                                    price={item.price}
                                                />
                                            ))
                                        }
                                    </>
                                ) : (<></>)
                            }
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
            <ExtraFooter/>
        </>
    )
}

export default HomePage