import './SecondCarousel.scss'
import Slider from "react-slick";

const SecondCarousel = () => {
    const settingsSecond = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 2,
    };
    return (
        <Slider {...settingsSecond} className='second-carousel'>
            <div className="carousel-item first">
                <h2>Техника Apple
                по доступным
                ценам</h2>
                <a href="#">987 обьявлениий</a>
            </div>
            <div className="carousel-item second">
                <h2>Брендовые
                товары за
                копейки</h2>
                <a href="#">987 обьявлениий</a>
            </div>
            <div className="carousel-item third">
                <h2>Все товары для
                офиса в одном
                месте</h2>
                <a href="#">987 обьявлениий</a>
            </div>
            <div className="carousel-item first">
                <h2>Техника Apple
                по доступным
                ценам</h2>
                <a href="#">987 обьявлениий</a>
            </div>
            <div className="carousel-item second">
                <h2>Брендовые
                товары за
                копейки</h2>
                <a href="#">987 обьявлениий</a>
            </div>
            <div className="carousel-item third">
                <h2>Все товары для
                офиса в одном
                месте</h2>
                <a href="#">987 обьявлениий</a>
            </div>
        </Slider>
    )
}

export default SecondCarousel