import Post from '../../components/Post'
import PostArr from '../PostArr/PostArr'

const NewPost = () => {
    return (
        <div className="posts-wrapper">
            {
                PostArr.map(item => (
                    <Post
                        img={item.img}
                        name={item.name}
                        time={item.date}
                        price={item.price}
                    />
                ))
            }
        </div>
    )
}

export default NewPost